import React, { Component, useState } from 'react';

import Persons from "../components/Person/Persons";
import MyContext from './MyContext';

const GlobalState = (props) => {

    const [getPersons, setPersons] = useState([]);
    const [getSinglePerson, setSinglePerson] = useState("");
    const [getShowPersons, setShowPersons] = useState(true);

    //Show Persons
  const handleShowPersons = () => {
      console.log(getShowPersons);
    setShowPersons(!getShowPersons);
  };

  //Delete Person
  const handleDeletePerson = (id) => {
    const persons = [...getPersons];
    const filteredPersons = persons.filter((p) => p.id !== id);

    setPersons(filteredPersons);
  };

  //Edit Person
  const handleNameChange = (event, id) => {
    const persons = [...getPersons];
    const personIndex = persons.findIndex((p) => p.id === id);

    getPersons[personIndex].fullName = event.target.value;

    setPersons(persons);
  };

  //Add Person
  const handleAddPerson = () => {
    const persons = [...getPersons];
    const person = {
      id: Math.floor(Math.random() * 1000),
      fullName: getSinglePerson,
    };

    if (person.fullName !== "" && person.fullName !== " ") {
      persons.push(person);
      setPersons(persons);
      setSinglePerson("");
    }
  };

  const setPerson = (event) => {
    setSinglePerson(event.target.value);
  };

  let personsList = null;

  if (getShowPersons) personsList = <Persons />;

    return ( 
        <MyContext.Provider
        value={{
            persons: getPersons,
            getSinglePerson: getSinglePerson,
            getShowPersons : getShowPersons,
            personsList : personsList,
            handleAddPerson: handleAddPerson,
            handleNameChange: handleNameChange,
            handleDeletePerson: handleShowPersons,
            handleDeletePerson: handleDeletePerson,
            handleShowPersons: handleShowPersons,
            setPerson: setPerson,
          }}>
              {props.children}
        </MyContext.Provider>
     );
}
 
export default GlobalState;