import React from "react";
import MyContext from "../../Context/MyContext";

import Person from "./Person";

const Persons = () => {
  return (
    <MyContext.Consumer>
      {(context) => (
        <div className="d-flex justify-content-center flex-wrap mx-5">
          {context.persons.map((person) => (  
            <Person
              key={person.id}
              fullName={123}
              deleteEvent={() => context.handleDeletePerson(person.id)}
              editEvent={(event) => context.handleNameChange(event, person.id)}
            />
          ))}
        </div>
      )}
    </MyContext.Consumer>
  );
};

export default Persons;
