import React, { Fragment, useState } from "react";

import MyContext from "./Context/MyContext";
import Header from "./components/common/Header";
import AddPersonForm from "./components/Person/AddPersonForm";

import Wraper from "./components/common/Wraper";

import "./index.css";
import "./App.css";

const App = (props) => {
  return (
    <MyContext.Consumer>
      {(context) => (
        <Fragment>
          <Header />

          <hr />

          <AddPersonForm />

          <button
            onClick={context.handleShowPersons}
            className={
              context.getShowPersons
                ? "mt-3 text-white btn btn-warning"
                : "mt-3 text-white btn btn-primary"
            }
          >
            Show/Hide Persons
          </button>

          {context.personsList}
        </Fragment>
      )}
    </MyContext.Consumer>
  );
};

export default Wraper(App, "text-center");
