import React from "react";

// const Wraper = ({ children,classes }) => {
//   return (
//       <div className={classes}>{children}</div>
//   );
// };

const Wraper = (RootComponent,classes) => {
  return props => (
    <div className={classes}>
        <RootComponent {...props} />
    </div>
  )
};

export default Wraper;
