import React, { render, useContext } from "react";

import MyContext from "./../../Context/MyContext";

const Header = () => {

  const context = useContext(MyContext);

  const { persons } = context;

  let badgeStyle = "";
  if (persons.length >= 3) badgeStyle = "badge-success";
  if (persons.length <= 2) badgeStyle = "badge-warning";
  if (persons.length <= 1) badgeStyle = "badge-danger";
  return(
    <MyContext.Consumer>
      {
      context => (
        <div>
          <h1
            style={{ fontSize: "3.5rem", fontFamily: "cursive" }}
            className="alert alert-info"
          >
            Persons Management
          </h1>
          <h4 className="alet alert-light my-4">
            Persons Count{" "}
            <span className={`badge badge-pill ${badgeStyle}`}>
              {persons.length}
            </span>
          </h4>
        </div>
      )}
    </MyContext.Consumer>
  );
};

export default Header;
