import React from "react";

import styles from './Person.css';


import propTypes from 'prop-types'

const Person = ({ fullName, deleteEvent, editEvent }) => {
  return (
    <div className="card bg-primary w-25 mx-5 my-5 p-5 text-white">
      <p className="h1">{`${fullName}`}</p>
      <div className="input-group w-100 mx-auto">
        <input type="text" onChange={editEvent} className="form-control py-4" />
        <div className="input-group-prepend">
          <button
            onClick={deleteEvent}
            className={`fa fa-trash ${styles.btn}`}
          />
        </div>
      </div>
    </div>
  );
};

Person.propTypes = {
  fullName : propTypes.string,
  deleteEvent : propTypes.func,
  editEvent : propTypes.func
}

export default Person;
